import React, { useState, useEffect } from "react";
import styles from "./InnerUser.module.css";
import { Row, Col } from "react-bootstrap";
import Chat from "student/components/Chat/Chat";
import { useSelector } from "react-redux";
import moment from "moment";
import { Redirect } from "react-router-dom";
import ImageCustom from "student/components/ImageCustom/ImageCustom";

const InnerUser = () => {
  const { receiver } = useSelector((s) => s.order);
  // console.log('receiver',receiver)
  if (!receiver) return <Redirect to="/" />;
  return (
    <div className={styles.bgLightWhite}>
      <div className={`${styles.bgSky} rounded-top`}>
        <div className="p-2 text-center">
          <h5>
            Name:
            <small className="text-muted">{receiver?.name}</small>
          </h5>
        </div>
        <div className="p-2 bg-white rounded-bottom">
          <div className="p-2">
            <Row>
              <Col md={4} className="p-0">
                <div className={`${styles.orderText} ${styles.bgSky} rounded`}>
                  <div className={styles.filedParagraph}>
                    <ImageCustom
                      className="img-fluid"
                      src={receiver?.image}
                      alt={receiver?.name}
                    />
                  </div>
                </div>
              </Col>
              <Col md={4} className="p-0">
                <div className={`${styles.orderText} ${styles.bgSky} rounded`}>
                  <p className={styles.filedParagraph}>
                    Name :
                    <small className="text-muted">
                      {" "}
                      {` ${receiver?.name}`}
                    </small>
                  </p>
                </div>
              </Col>
              <Col md={4} className="p-0">
                <div className={`${styles.orderText} ${styles.bgSky} rounded`}>
                  <p className={styles.filedParagraph}>
                    Email :
                    <small className="text-muted">
                      {` ${receiver?.email}`}
                    </small>
                  </p>
                </div>
              </Col>
              <Col md={4} className="p-0">
                <div className={`${styles.orderText} ${styles.bgSky} rounded`}>
                  <p className={styles.filedParagraph}>
                    Phone :
                    <small className="text-muted">{` ${receiver?.phone}`}</small>
                  </p>
                </div>
              </Col>
              <Col md={4} className="p-0">
                <div className={`${styles.orderText} ${styles.bgSky} rounded`}>
                  <p className={styles.filedParagraph}>
                    Since :
                    <small className="text-muted">
                      {` ${
                        receiver?.createdAt
                          ? moment(receiver?.createdAt).format("DD/MM/YYYY")
                          : ""
                      }`}
                    </small>
                  </p>
                </div>
              </Col>
              <Col md={4} className="p-0">
                <div className={`${styles.orderText} ${styles.bgSky} rounded`}>
                  <p className={styles.filedParagraph}>
                    Country :
                    <small className="text-muted">{` ${receiver?.country}`}</small>
                  </p>
                </div>
              </Col>
              <Col md={4} className="p-0">
                <div className={`${styles.orderText} ${styles.bgSky} rounded`}>
                  <p className={styles.filedParagraph}>
                    Orders :
                    <small className="text-muted">{` ${receiver?.orders}`}</small>
                  </p>
                </div>
              </Col>
            </Row>
          </div>
        </div>
      </div>

      <div className="mt-3">
        <div className="pr-5">
          <Chat />
        </div>
      </div>
    </div>
  );
};

export default InnerUser;
